import styled from "styled-components";
import gClef from "../../assets/img/g-clef.png";
import wholeNote from "../../assets/img/whole-note.png";

const Wrapper = styled.div`
  position: relative;
  margin-top: 40px;
  margin-bottom: 40px;
`;

const Staff = styled.div`
  width: 600px;
`;

const Line = styled.div`
  height: 1px;
  margin-bottom: 14px;
  background-color: #000;
`;

const Gclef = styled.img`
  position: absolute;
  top: -25px;
  left: 0;
  height: 116px;
`;

const WholeNote = styled.img`
  position: absolute;
  top: ${props => props.note && `${noteSteps[props.note]}px`};
  left: ${props =>
  props.index > 0
    ? `${(155 + (55 * props.index))}px`
    : '155px'};
  height: 14px;
`;

const noteSteps = {
  'c4': 68,
  'd4': 61,
  'e4': 54,
  'f4': 46,
  'g4': 39,
  'a4': 31,
  'b4': 24,
  'c5': 16,
  'd5': 9,
  'e5': 1,
  'f5': -6,
  'g5': -14,
  'a5': -21
};

const keyOfC = [
  'c4',
  'd4',
  'e4',
  'f4',
  'g4',
  'a4',
  'b4'
]

export default class MusicStaff extends React.Component {
  render() {
    return (
      <Wrapper>
        {keyOfC.map((note, index) => {

          console.log(index, note)
          return (
            <WholeNote src={wholeNote} note={note} index={index} />
          );
        })}
        <Gclef src={gClef} />
        <Staff>
          <Line />
          <Line />
          <Line />
          <Line />
          <Line />
        </Staff>
      </Wrapper>
    );
  }
}
